<template>
  <div>
    <el-button
      type="primary"
      plain
      icon="el-icon-plus"
      size="mini"
      @click="addRules"
      >新增模型</el-button
    >
    <el-table
      class="mt-10"
      :data="modelList"
      style="width: 100%"
      row-key="model_id"
      :load="loadChildren"
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
      @expand-change="handleExpand"
    >
      <el-table-column type="expand">
        <template #default="{ row }">
          <div
            v-if="row.children && row.children.length"
            class="nested-container"
          >
            <el-table
              :data="row.children"
              :show-header="true"
              style="width: 100%"
              :header-cell-style="{ background: '#f5f7fa', color: '#000000' }"
            >
              <el-table-column prop="class_code" label="编号">
              </el-table-column>
              <el-table-column prop="class_name" label="分类名称">
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button
                    type="text"
                    icon="el-icon-edit"
                    @click="editModelType(scope.row)"
                  />
                  <el-button type="text" @click="delData(scope.row)">
                    <i
                      title="删除"
                      class="el-icon-delete"
                      style="color: red"
                    ></i>
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <div v-else class="d-center">暂无子数据</div>
        </template>
      </el-table-column>
      <el-table-column label="序号" width="100">
        <template slot-scope="scope">{{ scope.$index + 1 }}</template>
      </el-table-column>
      <el-table-column prop="model_name" label="模型名称" align="center">
      </el-table-column>
      <el-table-column prop="model_type" label="模型类型" align="center">
        <template slot-scope="scope">
          {{ scope.row.model_type == 1 ? "rknn" : "大模型" }}
        </template>
      </el-table-column>
      <el-table-column prop="model_status" label="模型状态" align="center">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.model_status"
            :active-value="1"
            :inactive-value="2"
            @change="changeStatus(scope.row)"
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" @click="addModelType(scope.row)">
            <i title="新增" class="el-icon-circle-plus-outline"></i>
          </el-button>
          <el-button
            type="text"
            icon="el-icon-edit"
            @click="editData(scope.row)"
          />
          <el-button type="text" @click="delData(scope.row, 1)">
            <i title="删除" class="el-icon-delete" style="color: red"></i>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      :title="title"
      :visible.sync="addVisible"
      width="50%"
      :close-on-click-modal="false"
    >
      <el-form
        :model="addForm"
        ref="addForm"
        label-width="120px"
        class="mt-20"
        size="small"
      >
        <el-form-item label="模型名称:" prop="model_name">
          <el-input v-model="addForm.model_name" style="width: 40%"></el-input>
        </el-form-item>
        <el-form-item label="模型类型:" prop="model_type">
          <el-select v-model="addForm.model_type" size="small">
            <el-option label="rknn" :value="1"></el-option>
            <el-option label="大模型" :value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="模型:">
          <Upload :file="fileList" ref="Upload" />
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('addForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <el-dialog
      :title="typeTitle"
      :visible.sync="typeVisible"
      width="30%"
      :close-on-click-modal="false"
    >
      <el-form
        :model="addForm"
        ref="addForm"
        label-width="120px"
        class="mt-20"
        size="small"
      >
        <el-form-item label="模型名称:"
          >{{ updetaData.model_name }}
        </el-form-item>
        <el-form-item label="编号:" prop="class_code">
          <el-input v-model="addForm.class_code"></el-input>
        </el-form-item>
        <el-form-item label="分类名称:" prop="class_name">
          <el-input v-model="addForm.class_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="typeVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitTypeForm('addForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
import Upload from "@/components/upload.vue";
export default {
  components: {
    Upload,
  },
  data() {
    return {
      modelList: [],
      addForm: {},
      addVisible: false,
      title: "新增模型",
      addRulesObject: {},
      updetaData: {},
      typeTitle: "新增模型分类",
      typeVisible: false,
      fileList: [],
      loading: false,
    };
  },
  created() {
    this.getModelList();
  },
  methods: {
    async loadChildren(row, treeNode, resolve) {
      this.updetaData = row;
      // 模拟API请求
      try {
        const children = await this.fetchChildrenData(row.model_id);
        resolve(children);
      } catch (error) {
        console.error("加载子数据失败:", error);
        resolve([]);
      }
    },

    fetchChildrenData(model_id) {
      return new Promise((resolve) => {
        this.$api
          .get("/ai/getDataModelClassList", {
            params: {
              model_id: model_id,
            },
          })
          .then((res) => {
            resolve(res.data.data);
          });
      });
    },

    handleExpand(row, expanded) {
      if (expanded && !row.children) {
        this.loadChildren(row, null, (children) => {
          this.$set(row, "children", children);
        });
      }
    },
    changeStatus(row) {
      this.$api
        .post("/ai/setDataModelStatus", {
          model_id: row.model_id,
          model_status: row.model_status,
        })
        .then(() => {
          this.$message.success("操作成功");
          this.getModelList();
        })
        .catch(() => {
          row.model_status = row.model_status === 1 ? 2 : 1;
        });
    },
    //
    updetaClassData() {
      const parentIndex = this.modelList.findIndex(
        (item) => item.model_id === this.updetaData.model_id
      );
      if (parentIndex !== -1) {
        this.$api
          .get("/ai/getDataModelClassList", {
            params: {
              model_id: this.updetaData.model_id,
            },
          })
          .then((res) => {
            this.$set(this.modelList[parentIndex], "children", res.data.data);
          });
      }
    },
    // 提交分类
    submitTypeForm() {
      const isEditMode = !!this.addForm.class_id;
      const apiUrl = isEditMode
        ? "/ai/updateDataModelClass"
        : "/ai/addDataModelClass";
      this.addForm.class_code = Number(this.addForm.class_code);
      this.$api.post(apiUrl, this.addForm).then(() => {
        this.typeVisible = false;
        this.$message.success("操作成功"); // 优化刷新逻辑 - 只刷新当前展开的父级数据
        this.updetaClassData();
      });
    }, // 编辑分类
    editModelType(row) {
      this.typeVisible = true;
      this.addForm = { ...row };
    }, // 新增分类
    addModelType(row) {
      this.updetaData = row;
      this.addForm = {
        model_id: this.updetaData.model_id,
      };
      this.typeVisible = true;
    }, // 提交
    submitForm() {
      let formData = new FormData();
      this.addForm.model_file = this.$refs.Upload.fileList[0];
      if (this.isFile(this.addForm.model_file)) {
        formData.append("model_file", this.addForm.model_file);
      }
      formData.append("model_name", this.addForm.model_name);
      formData.append("model_type", this.addForm.model_type);
      const isEditMode = !!this.addForm.model_id;
      const apiUrl = isEditMode ? "/ai/updateDataModel" : "/ai/addDataModel";
      if (isEditMode) {
        formData.append("model_id", this.addForm.model_id);
      }
      this.$api
        .post(apiUrl, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then(() => {
          this.addVisible = false;
          this.$message.success("操作成功");
          this.getModelList();
        });
    }, // 删除
    delData(row, type) {
      const isClassDel = !!type;
      let url;
      let params;
      if (isClassDel) {
        url = "/ai/delDataModel";
        params = {
          model_id: row.model_id,
        };
      } else {
        url = "/ai/delDataModelClass";
        params = {
          class_id: row.class_id,
        };
      }
      this.$confirm("是否删除该数据?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$api.post(url, params).then(() => {
            this.$message.success("操作成功");
            if (isClassDel) {
              this.getModelList();
            } else {
              this.updetaClassData();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    }, // 修改模型
    editData(row) {
      this.updetaData = row;
      this.title = "编辑模型";
      this.addVisible = true;
      this.addForm = { ...row };
      this.fileList = this.addForm.model_file.split().map((item) => {
        return {
          name: item,
        };
      });
    }, // 新增模型
    addRules(row) {
      this.addForm = {};
      this.fileList = [];
      this.title = "新增模型";
      this.addVisible = true;
      this.updetaData = row;
    }, // 分类列表 - 优化后的load方法
    load(row) {
      if (!row.childrenLoaded) {
        this.getChildren(row, 1);
      }
    },
    getChildren(item) {
      item.loading = true;
      this.$api
        .get("/ai/getDataModelClassList", {
          params: {
            model_id: item.model_id,
          },
        })
        .then((res) => {
          item.children = res.data.data;
          this.$set(item, "loading", false);
          item.childrenLoaded = true;
          this.updetaData = item;
        })
        .finally(() => {
          this.$set(item, "loading", false);
        });
    },
    // 模型列表
    getModelList() {
      this.$api.get("/ai/getDataModelList").then((res) => {
        this.modelList = res.data.data.map((item) => {
          // // 保留已加载的子列表数据
          return {
            ...item,
            hasChildren: true,
          };
        });
      });
    },
    isFile(obj) {
      return (
        obj instanceof File ||
        (obj && typeof obj === "object" && obj.constructor.name === "File")
      );
    },
  },
};
</script>
<style lang="scss" scoped></style>
